/*!
*
*   external dependencies
*
!*/
@import '//fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic,900,900italic|Roboto+Condensed:300italic,400italic,700italic,400,300,700|Poiret+One:400|Source+Code+Pro:200,300,400,500,600,700,900';
/*!
*
*   app
*
!*/
/*
*   Bootstrap Overrides
*/
/*
*   Bootstrap Extensions
*/
/*
*   Custom Variables
*/
body {
  padding-bottom: 60px;
}
.navbar.navbar-default {
  border-radius: 0;
  border-top: none;
  border-right: none;
  border-left: none;
}
.navbar-header {
  display: table;
}
.navbar-header .navbar-brand {
  margin: 0;
  padding: 0;
  height: 50px;
  display: table-cell;
  vertical-align: middle;
}
.navbar-header .navbar-brand img {
  margin-top: 7px;
  max-width: 35vw;
}
@media (max-width: 767px) {
  .navbar-header .navbar-brand img {
    margin-left: 2vw;
  }
}
.navbar-nav {
  font-size: 8pt;
}
@media (min-width: 480px) {
  .navbar-nav {
    font-size: 10pt;
  }
}
.navbar-nav li > a {
  line-height: 50px;
  margin: 0;
  padding: 0 3vw;
}
@media (min-width: 480px) {
  .navbar-nav li > a {
    padding: 0 1.5em;
  }
}
.team.red,
.team.red a {
  color: #a94442;
}
.team.blue,
.team.blue a {
  color: #31708f;
}
.team.green,
.team.green a {
  color: #3c763d;
}
.team-bg.red {
  background-color: #f2dede;
}
.team-bg.blue {
  background-color: #d9edf7;
}
.team-bg.green {
  background-color: #dff0d8;
}
#overview .RegionMatches,
#overview .RegionWorlds {
  padding: 0 1em;
}
#overview .matchContainer + .matchContainer {
  margin-top: .5em;
  padding-top: .5em;
  border-top: 1px solid #eeeeee;
}
#overview .match {
  width: 100%;
  table-layout: fixed;
}
#overview .match tr:hover td {
  font-weight: 700;
}
#overview .match td {
  font-size: 9pt;
  line-height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (min-width: 480px) {
  #overview .match td {
    font-size: 10pt;
  }
}
#overview .match td.score {
  text-align: right;
  width: 30%;
}
#overview .match td.score .diff {
  display: inline-block;
}
#overview .match td.score .value {
  width: 4.5em;
  display: inline-block;
}
#overview .match td.pie {
  width: 70px;
  text-align: right;
}
#overview .match td.pie img {
  width: 60px;
  height: 60px;
}
#tracker .sprite {
  background-image: resolve('/img/sprite16.png');
  background-repeat: no-repeat;
  display: inline-block;
  width: 16px;
  height: 16px;
}
#tracker .sprite,
#tracker .sprite.neutral,
#tracker .sprite.camp,
#tracker .sprite.neutral.camp {
  background-position: 0px 0px;
}
#tracker .sprite.tower,
#tracker .sprite.neutral.tower {
  background-position: -17px 0px;
}
#tracker .sprite.keep,
#tracker .sprite.neutral.keep {
  background-position: -32px 0px;
}
#tracker .sprite.castle,
#tracker .sprite.neutral.castle {
  background-position: -48px 0px;
}
#tracker .sprite.red,
#tracker .sprite.red.camp {
  background-position: 0px -16px;
}
#tracker .sprite.red.tower {
  background-position: -17px -16px;
}
#tracker .sprite.red.keep {
  background-position: -32px -16px;
}
#tracker .sprite.red.castle {
  background-position: -48px -16px;
}
#tracker .sprite.blue,
#tracker .sprite.blue.camp {
  background-position: 0px -32px;
}
#tracker .sprite.blue.tower {
  background-position: -17px -32px;
}
#tracker .sprite.blue.keep {
  background-position: -32px -32px;
}
#tracker .sprite.blue.castle {
  background-position: -48px -32px;
}
#tracker .sprite.green,
#tracker .sprite.green.camp {
  background-position: 0px -48px;
}
#tracker .sprite.green.tower {
  background-position: -17px -48px;
}
#tracker .sprite.green.keep {
  background-position: -32px -48px;
}
#tracker .sprite.green.castle {
  background-position: -48px -48px;
}
#tracker .objective {
  transition: all 200ms ease-in;
  display: table;
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 12pt;
}
#tracker .objective.fresh {
  background-color: #ffc;
}
#tracker .objective > div {
  display: table-cell;
  vertical-align: middle;
}
#tracker .objective,
#tracker .objective > div {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#tracker .objective .objective-label {
  width: 100%;
}
#tracker .objective .objective-relative span {
  display: inline-block;
  width: 2em;
  text-align: center;
  font-size: 10pt;
}
#tracker .objective .objective-timestamp {
  padding-right: .5em;
  font-size: 10pt;
}
#tracker .objective .objective-map span {
  display: inline-block;
  width: 2.5em;
  text-align: center;
}
@media (min-width: 768px) {
  #tracker .objective {
    font-size: 9pt;
  }
}
@media (min-width: 992px) {
  #tracker .objective {
    font-size: 10pt;
  }
}
@media (min-width: 1200px) {
  #tracker .objective {
    font-size: 11pt;
  }
}
#tracker .objective .sprite {
  margin-right: .2em;
  position: relative;
  top: 2px;
}
#tracker .objective .direction {
  width: 8px;
  height: 8px;
  display: inline-block;
  margin-right: .2em;
}
#tracker .objective .direction img {
  width: 100%;
  height: 100%;
  opacity: .4;
}
#tracker .objective .guildname {
  font-size: 7pt;
  margin-right: 4px;
  cursor: pointer;
}
#tracker .objective .timer.countdown {
  transition: all 800ms ease-in;
  display: inline-block;
  height: 16px;
  color: #333;
  background-color: #ffc;
  line-height: 16px;
  font-size: 8pt;
}
#tracker .objective .timer.countdown.active {
  opacity: 1;
  padding: 0 .5em;
  max-width: 3em;
}
#tracker .objective .timer.countdown.inactive {
  opacity: 0;
  padding: 0;
  max-width: 0;
}
#tracker .objective.highlight .timer.countdown {
  background-color: #f00;
  color: #fff;
}
#tracker #scoreboards .scoreboard {
  margin: 0 1px;
  padding: .5em;
}
#tracker #scoreboards .scoreboard h1 {
  font-size: 18pt;
  font-weight: 400;
  white-space: nowrap;
  padding: 0;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}
#tracker #scoreboards .scoreboard h2 {
  font-size: 14pt;
  font-weight: 400;
  margin: .25em 0 .75em 0;
  padding: 0;
}
#tracker #scoreboards .scoreboard ul {
  margin: 0;
}
#tracker #scoreboards .scoreboard li {
  display: inline-block;
  line-height: 20px;
  height: 20px;
  font-size: 10pt;
}
#tracker #maps .map {
  padding: 1em .25em;
}
#tracker #maps .map h2 {
  cursor: pointer;
}
#tracker #maps .map.map-blur {
  scale: .9;
  blur: 1;
  opacity: .6;
}
#tracker #maps .mapScores {
  text-align: center;
}
#tracker #maps .mapScores h2 {
  font-size: 14pt;
  font-weight: 400;
  padding: 0 0 .25em;
  margin: 0 0 .1em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
#tracker #maps .mapScores li {
  font-size: 9pt;
}
#tracker #maps .map-section {
  margin: 0 0 20px 0;
  padding: 0;
}
@media (min-width: 768px) and (max-width: 991px) {
  #tracker #maps .map-section {
    padding: 0 .25em;
  }
}
#tracker #maps .map-section ul,
#tracker #maps .map-section li {
  margin: 0;
  padding: 0;
}
#tracker #maps .map-section .section-label {
  font-size: 10pt;
  line-height: 20px;
  font-weight: 700;
  margin: 0;
  padding: 0;
}
#tracker #log-container .log-tabs {
  border-bottom: 1px solid #eeeeee;
  padding-bottom: 4px;
  margin-bottom: 8px;
}
#tracker #log-container .log-tabs li {
  margin: 0;
  padding: 0;
}
#tracker #log-container .log-tabs a {
  margin: 0;
  padding: .2em .5em;
  font-size: 8pt;
  cursor: pointer;
}
#tracker #log-container .log-tabs #log-event-filters {
  margin-right: 2em;
}
#tracker #log-container .log-tabs #log-event-filters li {
  float: right;
}
#tracker #log-container #log {
  padding: 0;
  margin: 0 .5em;
  height: 250px;
  overflow-y: scroll;
  background: #ffffff repeat resolve('/img/rows-bg.png');
}
#tracker #log-container #log li {
  transition: all 200ms ease-in;
  opacity: 1;
  white-space: nowrap;
  overflow: hidden;
  display: block;
  font-size: 9pt;
  line-height: 20px;
  max-height: 20px;
}
#tracker #log-container #log li.hide-entry {
  max-height: 0;
  opacity: 0;
}
#tracker #log-container #log li:hover {
  background-color: rgba(255, 255, 0, 0.1);
}
#tracker #log-container #log li .objective {
  font-size: 9pt;
}
#tracker #log-container #log .objective-relative,
#tracker #log-container #log .objective-timestamp,
#tracker #log-container #log .objective-map,
#tracker #log-container #log .objective-guild {
  font-size: 9pt;
}
#tracker #log-container #log .objective-event {
  font-size: 7pt;
}
#tracker #options {
  transition: all 400ms ease-in;
  padding-right: 1em;
}
#tracker #options .sub-options {
  padding-left: 1em;
}
#tracker #options .disabled {
  opacity: 0.8;
  blur: .5;
}
#tracker #options h3 .fa-stack {
  font-size: 60%;
}
#tracker #guilds .guild + .guild {
  margin-top: 2em;
}
#tracker #guilds .guild:nth-child(even) {
  background-color: #f6f6f6;
}
#tracker #guilds .guild h1 {
  font-size: 32pt;
  font-size: 24pt;
}
@media (min-width: 992px) {
  #tracker #guilds .guild h1 {
    font-size: 32pt;
  }
}
#tracker #guilds .guild h1 a {
  color: #333333;
}
#tracker #guilds .guild img.emblem {
  width: 256px;
  max-width: 100%;
  height: auto;
}
#tracker #guilds .guild ul {
  margin-left: 1em;
}
#tracker .section-header {
  margin: 1em 0 .5em;
  padding: 0 0 .25em;
  border-bottom: 1px solid #eee;
}
